
import Toast from 'vux/src/components/toast/index.vue'
import XHeader from 'vux/src/components/x-header/index.vue'

export default {
mounted() {

this.$refs.scroll.$el.style.height = document.body.clientHeight - 94 + 'px'
this.initdata();
},
components: {
XHeader,
Toast,
},
data() {
return {
noData: false,
list: [],
pageIndex: 1,
pageSize: 15,
}
},
methods: {
initdata() {
var myDate = new Date();
this.start = myDate.getFullYear() + "" + (myDate.getMonth() + 1);
this.$ajax.post('', {
'MSH.1': 'qtylfw',
'MSH.2': 'qryList',
'GRDAID':this.$root.loginInfo.grdaid,
'pageIndex': this.pageIndex,
'pageSize': this.pageSize,
'CHZBM': this.$root.loginInfo.CHZBM,
'token': this.$root.loginInfo.token,
}).then(data => {
this.$refs.scroll.finishPullUp();
this.$refs.scroll.finishPullDown();
if (typeof data != 'undefined') { //判断是否空对象
if (typeof data.rows != 'undefined' && data.rows.length > 0) {

if (this.pageIndex == 1) {
this.list = data.rows
} else {
this.list = this.list.concat(data.rows);
}
this.noData = false;
} else {
this.judge()
}
} else {
this.judge()
}
}).catch(error => {
this.judge()
console.log(error);
});
},
judge() { //无数据判断
if (this.pageIndex > 1) {
this.$vux.toast.text('没有更多了', 'bottom');
} else {
this.list = [];
this.noData = true;
}
},
pullingDown() {
this.pageIndex = 1;
this.initdata();
},
pullingUp() {
this.pageIndex++;
this.initdata();
},
cancerDetail(cbh, type, tm) { //点击跳转
this.$router.push({
name: "diagnosisRrecords",
params: {
TYPE: type,
CBH: cbh,
TM: tm
}
});
},
}
}



